@import url('https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,300;0,400;0,500;1,300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 2.4rem;
  font-family: sans-serif;
  color: #5a3e2b;
  font-family: 'Quicksand';
  font-weight: 500;
}

.app {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

h1 {
  text-align: center;
  background-color: #f4a226;
  font-family: inherit;
  font-size: 8rem;
  font-weight: 700;
  word-spacing: 30px;
  letter-spacing: -5px;
  padding: 2.4rem 0;
  white-space: nowrap;
}

.add-form {
  background-color: #e5771f;
  padding: 2.8rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}

.add-form div {
  display: flex;
  gap: 1rem;
}

h3 {
  font-size: 2.4rem;
}

button,
select,
input {
  background-color: #ffebb3;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 2rem;
  padding: 1.2rem 2rem;
  font-weight: 700;
  font-size: 1.8rem;
  cursor: pointer;
}

button,
select,
input {
  outline: none;
}

.add-form button {
  text-transform: uppercase;
  background-color: #76c7ad;
}

.list {
  background-color: #5a3e2b;
  color: #ffebb3;
  padding: 4rem 0;

  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
}

.actions button,
.list select {
  text-transform: uppercase;
  padding: 0.8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0.8rem;
}

.actions button {
  padding: 1rem 2.4rem;
}

.list ul {
  list-style: none;
  width: 80%;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.2rem;
  justify-content: center;
  align-content: start;

  margin-bottom: auto;
}

.list li {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

input[type='checkbox'] {
  height: 2rem;
  width: 2rem;
  accent-color: #e5771f;
}

.list li button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 3rem;
  padding: 0.5rem;
  color: inherit;
}

.list li button:hover {
  color: #d7122f;
}

.stats {
  background-color: #76c7ad;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
}

.popup-container {
  z-index: 1;
  display: flex;
  position: fixed;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3);
}

.pop-up {
  background-color: #f4a226;
  padding: 1.8rem;
  border-radius: 2rem;
  flex-shrink: 3;
}

.pop-up p {
  font-size: 1.8rem;
  margin-bottom: 2.5rem;
}

.pop-up div {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.pop-up button:first-of-type {
  background-color: #d7122f;
  color: #ffebb3;
}

.pop-up button:hover,
.actions button:hover {
  filter: brightness(0.85);
}

@media only screen and (max-width: 899px) {
  body {
    h1 {
      font-size: 10vw;
    }

    .add-form {
      flex-direction: column;
      padding: 2rem 0;
    }

    .add-form h3 {
      font-size: 2rem;
    }

    button,
    select,
    input {
      font-family: inherit;
      border: none;
      border-radius: 2rem;
      padding: 0.9rem 1.7rem;
      font-weight: 700;
      font-size: 1.7rem;
      cursor: pointer;
    }

    .stats {
      padding: 1.8rem 1rem;
    }

    .pop-up {
      max-width: 345px;
    }

    .pop-up p {
      font-size: 1.7rem;
      margin-bottom: 1.5rem;
    }
  }
}
